import React  from 'react';
import Inputs from '../components/Inputs';
function Grid(props) {
    return (
        <div className = "container">
            <h3>Подоконник</h3>
            <table className="table table-light text-center">
                <thead className="thead-light">
                    <tr>
                        <th>Цветной</th>
                        <th>Белый</th>
                        <th>Ширина</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        
                       props.grid.data.map((x) =>
                            <tr>
                               <td><Inputs type="text" value={x.color} inputClass = "text-center"/></td>
                               <td><Inputs type="text" value={x.white} inputClass="text-center" /></td>
                               <td><Inputs type="text" value={x.size} inputClass="text-center" /></td>
                            </tr>
                        )

                    }
                </tbody>
            </table>
        </div>
    )
} export default Grid;