import React from 'react';
function Inputs(props) {
    return (
        <div className="col">
            <div className="form-group">
                <label htmlFor={`${props.id}`}><h3>{props.names}</h3></label>
                <input type={props.type} defaultValue = {props.value} className={`form-control ${props.inputClass}`} id={`${props.id}`} onChange={(e) => props.onChangeInput(e.target.value)} />
            </div>
        </div>
    )
} export default Inputs;