import React from 'react';
import ChecedsClick from './ChecedsClick';
import Deleteicon from './Deleteicon';

function Articles(props) {
    return (
        <div className="col-sm mt-3">
            {(props.menuartid.length > 0)?<div><div className="row"><div className="col-2"><h3>Статьи</h3></div> <div className="col">{(props.arrchecedsmenuartdel.length > 0) ? <div onClick={() => { props.getmenuartdelete(props.arrchecedsmenuartdel.join(',')); props.getarrchecedsmenuartdelremove()}}><Deleteicon  title = "Удалить статью"/></div>:''}</div></div>
            {props.menuartid.map((x, i) => <div key={i + 5 } onClick={() => { props.getarrchecedsmenuartdel(props.arrchecedsmenuartdel, x.art_id, { b: true, k: props.arrchecedsmenuartdel });  }}><div className="row"><div className="col-0 p-0"><ChecedsClick c={props.arrchecedsmenuartdel[props.arrchecedsmenuartdel.indexOf(x.art_id)]}/></div><div className = "col">{x.art_name}</div></div></div>)}
            {(props.menuartid.length === 0)?<div>Нет статей</div>:''}</div>:<div></div>}
            <div className="col"><h3>Привязать статью</h3></div>
            {props.articles.map((x, i) =>  <div key={x.art_name + 5} onClick={() => { props.getarrchecedsmenuartadd(props.arrchecedsmenuartadd, x.art_id, { b: true, k: props.arrchecedsmenuartadd }) }}><div className="row"><div className="col-0 p-0"><ChecedsClick c={props.arrchecedsmenuartadd[props.arrchecedsmenuartadd.indexOf(x.art_id)]} /></div><div className="col">{x.art_name}</div></div></div>)}

        </div>
    )
} export default Articles;