 function arrcheceds(state = [], action) {
    switch (action.type) {
        case 'ARRCHECEDS':
            return action.payload;
        default:
            return state;
    }

}

 function arrchecedsmenuartdel(state = [], action) {
    switch (action.type) {
        case 'ARRCHECEDSMENUARTDEL':
            return action.payload;
        case 'ARRCHECEDSMENUARTDELREMOVE':
            return [];
        default:
            return state
    }

}

 function arrchecedsimg(state = [], action) {
    switch (action.type) {
        case 'ARRCHECEDSIMG':
            let c = action.ars.concat();
            if (c.indexOf(action.e) === -1) {
                c.splice(0, 1, action.e);
                action.f = { ...action.f, b: true, k: c };
            } else {
                c.splice(c.indexOf(action.e), 1)
                action.f = { ...action.f, b: false, k: c };
            }
            return c;
        case 'ARRCHECEDSIMGREMOVE':
            return [];
        default:
            return state
    }

}
 function arrchecedsmenuartadd(state = [], action) {
    switch (action.type) {
        case 'ARRCHECEDSMENUARTADD':
            return action.payload;
        case 'ARRCHECEDSMENUARTADDREMOVE':
            return [];
        default:
            return state
    }

}

