import React from 'react';
import { Link } from 'react-router-dom';
function Saves(props) {

    return (

        <div className="row">
            <div className="row">
                <button className="btn btn-primary ml-5 mt-3 mb-3" type="submit">Сохранить</button>
                <Link className="btn btn-primary ml-3 mt-3 mb-3" to={props.lincks}>Закрыть</Link>
            </div>
        </div>

    )
} export default Saves;