import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { replaceUrls } from '../redux/actions';
import ContentDText from '../components/ContentDText';
const Insertinput = (props) => {
    const [contenttext, setcontenttrxt] = useState('');
    useEffect(() => {
        props.getinputnames({ type: 'INPUTNAMES', payload: '' });
        props.getinputalias({ type: 'INPUTALIAS', payload: '' });
        props.getinputtitle({ type: 'INPUTTITLE', payload: '' });
        props.getinputkeyword({ type: 'INPUTKEYWORD', payload: '' });
        props.getinputdescript({ type: 'INPUTDESCRIPT', payload: '' });
        props.getinputsubcontent({ type: 'INPUTSUBCONTENT', payload: '' });
        props.getinputcontent({ type: 'INPUTCONTENT', payload: '' });
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.menuid]);

    useEffect(() => {
        props.getinputcontent({ type: 'INPUTCONTENT', payload: contenttext });
    }, [contenttext]);
    return (
        <div className="col">

            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_name"><h3>Название *</h3></label>
                    <input type="text" className="form-control" id="menu_name" onChange={(e) => { props.getinputnames({ type: 'INPUTNAMES', payload: e.target.value }); props.getinputalias({ type: 'INPUTALIAS', payload: replaceUrls(e.target.value) }) }} />
                </div>
            </div>

            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_alias"><h3>Адрес страницы *</h3></label>
                    <input type="text" value={props.inputalias} readOnly className="form-control" id="menu_alias" />
                </div>
            </div>
            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_alias"><h3>Заголовок страницы *</h3></label>
                    <input type="text" onChange={(e) => props.getinputtitle({ type: 'INPUTTITLE', payload: e.target.value })} className="form-control" id="menu_alias" />
                </div>
            </div>


            <div className="col-sm">
                <label htmlFor="menu_keyword"><h3>Ключевые слова</h3></label>
                <textarea className="form-control" id="menu_keyword" onChange={(e) => props.getinputkeyword({ type: 'INPUTKEYWORD', payload: e.target.value })} rows={3} />
                <label htmlFor="menu_descript"><h3>Краткое описание</h3></label>
                <textarea className="form-control" id="menu_descript" onChange={(e) => props.getinputdescript({ type: 'INPUTDESCRIPT', payload: e.target.value })} rows={3} />
            </div>
            {
                (props.content === "true") ?
                    <div>
                        <div className="col">
                            <label htmlFor="subcontent"><h3>Краткое описание статьи</h3></label>
                            <textarea className="form-control" id="subcontent" onChange={(e) => props.getinputsubcontent({ type: 'INPUTSUBCONTENT', payload: e.target.value })} rows={5} />
                        </div>
                        <div className="col">
                            <label htmlFor="content"><h3>Текст статьи</h3></label>
                            <ContentDText setcontenttrxt={setcontenttrxt} inputcontent={''} />
                        </div>
                    </div> : ''
            }

        </div>)
}


const setPath = state => ({
    menuid: state.menuid,
    inputnames: state.inputnames,
    inputalias: state.inputalias,
    inputtitle: state.inputtitle,
    inputkeyword: state.inputkeyword,
    inputdescript: state.inputdescript,
    inputsubcontent: state.inputsubcontent,
    inputcontent: state.inputcontent
})

const toProps = dispatch => ({
    getinputnames: (e) => dispatch(e),
    getinputalias: (e) => dispatch(e),
    getinputtitle: (e) => dispatch(e),
    getinputkeyword: (e) => dispatch(e),
    getinputdescript: (e) => dispatch(e),
    getinputsubcontent: (e) => dispatch(e),
    getinputcontent: (e) => dispatch(e)
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Insertinput);