import React, { useState, useEffect } from 'react';
import './css/App.css';
import Menu from './components/Menu';
import {
  Route, Redirect, Switch
} from "react-router-dom";
import Admin from './pages/Admin';
import Main from './pages/Main';
import Menuinsert from './pages/Menuinsert';
import Menuupdate from './pages/Menuupdate';
import Articlesmain from './pages/Articlesmain';
import Articlesinsert from './pages/Articlesinsert';
import Articlesupdate from './pages/Articlesupdate';
import Files from './pages/Files';
import Prais from './pages/Prais';
import Sittens from './pages/Sittens';
import menu from './db/menu.json';
function App() {

  return (
    <div className="App container-fluid p-0" >
      <div className="text-center" style={{ height: '50px', backgroundColor: 'green', color: '#ffffff', fontSize: '18pt' }}>Административная панель</div>

      <div className="row">
        <div className="col-1" style={{ borderRight: 'solid 2px #cccccc', width: '200px' }}>
          <Menu menu={menu} linckClass={''} />
        </div>
        <div className="col-sm p-0">
            <Redirect from='/admin' to='/admin/index.html' />
            <Route exact path="/admin/index.html" component={Admin} />
            <Route path="/admin/index.html/menu/:id" component={Main} />
            <Route path="/admin/index.html/menuinsert" component={Menuinsert} />
            <Route path="/admin/index.html/menuupdate/:id" component={Menuupdate} />
            <Route path="/admin/index.html/articles/:id" component={Articlesmain} />
            <Route path="/admin/index.html/articlesinsert" component={Articlesinsert} />
            <Route path="/admin/index.html/articlesupdate/:id" component={Articlesupdate} />
            <Route path="/admin/index.html/files" component={Files} />
            <Route path="/admin/index.html/prais" component={Prais} />
            <Route path="/admin/index.html/sittens" component={Sittens} />
          
        </div>
      </div>

    </div>
  );
}

export default App;
