import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { artupdate_a, art_a, images_a, menuupdate_a, menuartdelete_a, artid_a, } from '../redux/actions';
import { Link } from 'react-router-dom';
import Artyesno from '../components/Artyesno';
import Updateinput from '../components/Updateinput';
import Images from '../components/Images';
import Saves from '../components/Saves';
function Articlesupdate(props) {
    const [params, setparams] = useState(0);
    const [img, setimg] = useState('');
    useEffect(() => {
        props.getarticles();
        props.getimages();
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        props.getartid(props.match.params.id)
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.match.params.id]);

    useEffect(() => {
        setimg(props.articlesid.art_img);
        props.getarrchecedsimg(props.arrchecedsimg, img, { b: true, k: props.arrcheceds });
        setparams(props.articlesid.params);
    }, [props.articlesid, props.match]);

    function handleSubmit(event) {
        props.getartremove(
            props.arrchecedsimg.join(''),
            props.inputnames,
            props.inputtitle,
            props.inputalias,
            props.inputkeyword,
            props.inputdescript,
            props.inputsubcontent,
            props.inputcontent,
            params,
            props.articlesid.art_id
        );
        event.preventDefault();
    }

    return (
        <div className="container-fluid" >
            <h2>Создание нового пункта меню</h2>
            <form onSubmit={handleSubmit}>
                <Saves lincks={`/admin/index.html/articles/1`} />
                <div className="row">
                    <Updateinput input={{
                        names: props.articlesid.art_name,
                        alias: props.articlesid.art_alias,
                        title: props.articlesid.art_title,
                        keyword: props.articlesid.art_keyword,
                        descript: props.articlesid.art_descript,
                        subcontent: props.articlesid.art_subcontent,
                        content: props.articlesid.art_content
                    }}
                        content="true"
                    />
                    <Artyesno setparams={setparams} params={params} />

                </div>
                <div className="col">
                    <Images images={props.images} arrcheceds={props.arrchecedsimg} getarrcheceds={props.getarrchecedsimg} />
                </div>
            </form>
        </div>
    )
}
const setPath = state => ({
    inputnames: state.inputnames,
    inputalias: state.inputalias,
    inputtitle: state.inputtitle,
    inputkeyword: state.inputkeyword,
    inputdescript: state.inputdescript,
    inputsubcontent: state.inputsubcontent,
    inputcontent: state.inputcontent,
    articles: state.articles,
    articlesid: state.articlesid,
    images: state.images,
    arrchecedsimg: state.arrchecedsimg,
    artremove: state.artremove
})

const toProps = dispatch => ({

    getarticles: () => dispatch(art_a()),
    getartid: (id) => dispatch(artid_a(id)),
    getmenuupdate: (artid, name, alias, title, keyword, descript, content, img, parent, id, art) => dispatch(menuupdate_a(artid, name, alias, title, keyword, descript, content, img, parent, id, art)),
    getarrchecedsimg: (ars, e, f) => dispatch({ type: 'ARRCHECEDSIMG', ars: ars, e: e, f: f }),
    getimages: () => dispatch(images_a()),
    getmenuartdelete: (id) => dispatch(menuartdelete_a(id)),
    getartremove: (art_img, art_name, art_title, art_alias, art_keyword, art_descript, art_subcontent, art_content, params, art_id) => dispatch(artupdate_a(art_img, art_name, art_title, art_alias, art_keyword, art_descript, art_subcontent, art_content, params, art_id))
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Articlesupdate);