import React from 'react';
import { Link } from 'react-router-dom';
import '../css/menu.css';
function Menu(props) {
    return (
        <nav className="nav flex-column menu" style={{ width: '200px' }}>
            {props.menu.map((x) =>
                <li key={x.name} className={`nav-item`}>
                    <Link className={`nav-link active ${props.linckClass}`} to={x.alias}>
                        {x.name}
                    </Link>
                </li>
            )}
        </nav>

    )
}
export default Menu;
