import React, { useState, useEffect } from 'react';

function Artyesno(props) {
    const [state, setstate] = useState({ yes: 'red', no: 'green' })
    
    useEffect(() => {
        if (props.params === '1'){
           setstate({ yes: 'green', no: 'red' });
    }
        if (props.params === '0'){
           setstate({ yes: 'red', no: 'green' });
    }
    }, [props.params]);
   
    return (
        <div className="text-center col-3 mr-5">
            <h3 className = "pb-2">Основная на главной странице</h3>
            <div className="row" style = {{cursor:'pointer'}}>
                <div className="col artyes" onClick={() => {  props.setparams('1');}} style={{ backgroundColor: state.yes, color:'#ffffff',paddingTop:'4px', paddingBottom:'4px'}}>Да</div>
                <div className="col artno" onClick={() => { props.setparams('0');}}  style={{ backgroundColor: state.no, color: '#ffffff', paddingTop: '4px', paddingBottom: '4px'}}>Нет</div>
            </div>
        </div>
    )
} export default Artyesno;