import React, { useState, useEffect } from 'react';
import {art_a, images_a, menuupdate_a, menuartdelete_a, artid_a, artinsert_a } from '../redux/actions';
import { connect } from 'react-redux';
import Images from '../components/Images';
import Artyesno from '../components/Artyesno';
import Insertinput from '../components/Insertinput';
import Saves from '../components/Saves';
function Articlesinsert(props) {
    const [params, setparams] = useState(0);
    useEffect(() => {
        props.getimages();
    }, []);

    function handleSubmit(event) {
        props.getartinsert(props.arrchecedsimg[0], props.inputnames, props.inputtitle, props.inputalias, props.inputkeyword, props.inputdescript, props.inputsubcontent, props.inputcontent, params)
        event.preventDefault();
    }

    return (
        <div className="container-fluid" >
            <h2>Создание нового пункта меню</h2>
            <form onSubmit={handleSubmit}>
                <Saves lincks={`/admin/index.html/articles/1`} />
                <div className="row">
                    <Insertinput content="true" />
                    <Artyesno setparams={setparams} params={params} />
                </div>
                <div className="col">
                    <Images images={props.images} arrcheceds={props.arrchecedsimg} getarrcheceds={props.getarrchecedsimg} />
                </div>
            </form>
        </div>
    )
}
const setPath = state => ({
    menu: state.menu,
    menuid: state.menuid,
    menuartid: state.menuartid,
    articles: state.articles,
    inputnames: state.inputnames,
    inputalias: state.inputalias,
    inputtitle: state.inputtitle,
    inputkeyword: state.inputkeyword,
    inputdescript: state.inputdescript,
    inputsubcontent: state.inputsubcontent,
    inputcontent: state.inputcontent,
    images: state.images,
    arrchecedsimg: state.arrchecedsimg,
    arrchecedsmenuartdel: state.arrchecedsmenuartdel,
    arrchecedsmenuartadd: state.arrchecedsmenuartadd,
    artinsert: state.artinsert

})

const toProps = dispatch => ({
    getarticles: () => dispatch(art_a()),
    getartid: (id) => dispatch(artid_a(id)),
    getmenuupdate: (artid, name, alias, title, keyword, descript, content, img, parent, id, art) => dispatch(menuupdate_a(artid, name, alias, title, keyword, descript, content, img, parent, id, art)),
    getarrchecedsimg: (ars, e, f) => dispatch({ type: 'ARRCHECEDSIMG', ars: ars, e: e, f: f }),
    getimages: () => dispatch(images_a()),
    getmenuartdelete: (id) => dispatch(menuartdelete_a(id)),
    getartinsert: (art_img, art_name, art_title, art_alias, art_keyword, art_descript, art_subcontent, art_content, params) => dispatch(artinsert_a({ art_img: art_img, art_name: art_name, art_title: art_title, art_alias: art_alias, art_keyword: art_keyword, art_descript: art_descript, art_subcontent: art_subcontent, art_content: art_content, params: params}))

})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Articlesinsert);