import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Updateinput from '../components/Updateinput';
import '../css/menuupdate.css'
import { connect } from 'react-redux';
import { menu_a, menuid_a, menuartid_a, art_a, images_a, menuupdate_a, menuartdelete_a, checeds } from '../redux/actions';
import Menutether from '../components/Menutether';
import Images from '../components/Images';
import Articles from '../components/Articles';
import Saves from '../components/Saves';
function Menuupdate(props) {
    const [img, setimg] = useState('');
    const [parent, setparent] = useState(0);
    useEffect(() => {
        props.getmenu();
        props.getarticles();
        props.getimages();
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        props.getmenuid(props.match.params.id);
        props.getmenuartid(props.match.params.id);

    }, [props.match.params.id, props.menuupdate, props.menuartdelete]);

    useEffect(() => {
        setimg((props.menuupdate.menu_img) ? props.menuupdate.menu_img : props.menuid.menu_img)
        props.getarrchecedsimg(props.arrchecedsimg, img, { b: true, k: props.arrcheceds })
    }, [props.menuid, props.match, props.menuupdate]);
    function handleSubmit(event) {
        props.getmenuupdate(props.menuid.menu_art_id, props.inputnames, props.inputalias, props.inputtitle, props.inputkeyword, props.inputdescript, '', props.arrchecedsimg.join(''), parent, props.menuid.menu_id, props.arrchecedsmenuartadd.join(''));
        props.getarrchecedsmenuartaddremove([], 0, []);
        event.preventDefault();
    }
    return (
        <div className="container-fluid" >
            <div className="row">
                <div className="col">
                    <h2>Редактировать пункт меню</h2>
                </div>
            </div>
            <form onSubmit={handleSubmit}  >
                <Saves lincks={`/admin/index.html/menu/${props.menuid.menu_art_id}`} />
                <div className="row">
                    <Updateinput input={{
                        names: props.menuid.menu_name,
                        alias: props.menuid.menu_alias,
                        title: props.menuid.menu_title,
                        keyword: props.menuid.menu_keyword,
                        descript: props.menuid.menu_descript,
                        subcontent: '',
                        content: '',


                    }} />
                    <div className="col-5">
                        <Menutether menu={props.menu} menuid={props.menuid} setparent={setparent} />
                        <Articles getarrchecedsmenuartdelremove={props.getarrchecedsmenuartdelremove} getmenuartdelete={props.getmenuartdelete} getarrchecedsmenuartdel={props.getarrchecedsmenuartdel} arrchecedsmenuartadd={props.arrchecedsmenuartadd} getarrchecedsmenuartadd={props.getarrchecedsmenuartadd} arrchecedsmenuartdel={props.arrchecedsmenuartdel} menuartid={props.menuartid} articles={props.articles} arrcheceds={props.arrcheceds} getarrcheceds={props.getarrcheceds} />
                    </div>
                </div>
                <div className="col">
                    <Images images={props.images} arrcheceds={props.arrchecedsimg} getarrcheceds={props.getarrchecedsimg} />
                </div>

            </form>
        </div>
    )
}
const setPath = state => ({
    menu: state.menu,
    menuid: state.menuid,
    menuartid: state.menuartid,
    articles: state.articles,
    inputnames: state.inputnames,
    inputalias: state.inputalias,
    inputtitle: state.inputtitle,
    inputkeyword: state.inputkeyword,
    inputdescript: state.inputdescript,
    images: state.images,
    arrchecedsimg: state.arrchecedsimg,
    arrchecedsmenuartdel: state.arrchecedsmenuartdel,
    arrchecedsmenuartadd: state.arrchecedsmenuartadd,
    menuupdate: state.menuupdate,
    menuartdelete: state.menuartdelete
})

const toProps = dispatch => ({
    getmenu: () => dispatch(menu_a()),
    getmenuupdate: (artid, name, alias, title, keyword, descript, content, img, parent, id, art) => dispatch(menuupdate_a(artid, name, alias, title, keyword, descript, content, img, parent, id, art)),
    getmenuid: (id) => dispatch(menuid_a(id)),
    getmenuartid: (id) => dispatch(menuartid_a(id)),
    getarticles: () => dispatch(art_a()),
    getarrchecedsimg: (ars, e, f) => dispatch({ type: 'ARRCHECEDSIMG', ars: ars, e: e, f: f }),
    getarrchecedsmenuartdel: (ars, e, f) => dispatch({ type: 'ARRCHECEDSMENUARTDEL', payload: checeds(ars, e, f) }),
    getarrchecedsmenuartdelremove: (ars, e, f) => dispatch({ type: 'ARRCHECEDSMENUARTDELREMOVE', ars: ars, e: e, f: f }),
    getarrchecedsmenuartadd: (ars, e, f) => dispatch({ type: 'ARRCHECEDSMENUARTADD', payload: checeds(ars, e, f) }),
    getarrchecedsmenuartaddremove: (ars, e, f) => dispatch({ type: 'ARRCHECEDSMENUARTADDREMOVE', ars: ars, e: e, f: f }),
    getimages: () => dispatch(images_a()),
    getmenuartdelete: (id) => dispatch(menuartdelete_a(id))
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Menuupdate);