import React from 'react';
import ChecedsClick from './ChecedsClick';
import '../css/images.css';
function Images(props) {
    return (
        <div>
            <h3>Изображение</h3>
            <div className="row"> {Object.values(props.images).map((x, i) => <div onClick={() => { props.getarrcheceds(props.arrcheceds, x, { b: true, k: props.arrcheceds }) }} className={((props.arrcheceds[props.arrcheceds.indexOf(x)])) ? "col-2 text-center img imgactive" :" col-2 text-center img "} key={i + 245}  ><ChecedsClick c={props.arrcheceds[props.arrcheceds.indexOf(x)]} /><img  src={`/img/window/${x}`}/></div>)}</div>
        </div>
    )
} export default Images;