import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { replaceUrls, inputs } from '../redux/actions';
import ContentDText from './ContentDText';
const Ubdateinput = (props) => {
    const [contenttext, setcontenttrxt] = useState('');
    useEffect(() => {
        props.getinputnames({ type: 'INPUTNAMES', payload: inputs(props.input.names) });
        props.getinputalias({ type: 'INPUTALIAS', payload: inputs(replaceUrls(props.input.names)) });
        props.getinputtitle({ type: 'INPUTTITLE', payload: inputs(props.input.title) });
        props.getinputkeyword({ type: 'INPUTKEYWORD', payload: inputs(props.input.keyword) });
        props.getinputdescript({ type: 'INPUTDESCRIPT', payload: inputs(props.input.descript) });
        props.getinputsubcontent({ type: 'INPUTSUBCONTENT', payload: inputs(props.input.subcontent) });
        props.getinputcontent({ type: 'INPUTCONTENT', payload: inputs(props.input.content) });
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.input.names, props.input.title, props.input.keyword, props.input.descript, props.input.subcontent, props.input.content]);

    useEffect(() => {
        props.getinputcontent({ type: 'INPUTCONTENT', payload: inputs(contenttext) });
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, [contenttext, props.input.content]);
    return (
        <div className="col">

            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_name"><h3>Название *</h3></label>
                    <input defaultValue={props.input.names} type="text" className="form-control" id="menu_name" onChange={(e) => { props.getinputnames({ type: 'INPUTNAMES', payload: e.target.value }); props.getinputalias({ type: 'INPUTALIAS', payload: replaceUrls(e.target.value) }) }} />
                </div>
            </div>

            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_alias"><h3>Адрес страницы *</h3></label>
                    <input type="text" value={props.inputalias} readOnly className="form-control" id="menu_alias" />
                </div>
            </div>
            <div className="col">
                <div className="form-group">
                    <label htmlFor="menu_alias"><h3>Заголовок страницы *</h3></label>
                    <input type="text" defaultValue={props.input.title} onChange={(e) => props.getinputtitle({ type: 'INPUTTITLE', payload: e.target.value })} className="form-control" id="menu_alias" />
                </div>
            </div>
            <div className="col">
                <label htmlFor="menu_keyword"><h3>Ключевые слова</h3></label>
                <textarea className="form-control" id="menu_keyword" onChange={(e) => props.getinputkeyword({ type: 'INPUTKEYWORD', payload: e.target.value })} rows={3} defaultValue={props.input.keyword} />
            </div>
            <div className="col">
                <label htmlFor="menu_descript"><h3>Краткое описание</h3></label>
                <textarea className="form-control" id="menu_descript" onChange={(e) => props.getinputdescript({ type: 'INPUTDESCRIPT', payload: e.target.value })} rows={3} defaultValue={props.input.descript} />
            </div>
            {
                (props.content === "true") ?
                    <div>
                        <div className="col">
                            <label htmlFor="subcontent"><h3>Краткое описание статьи</h3></label>
                            <textarea className="form-control" id="subcontent" onChange={(e) => props.getinputsubcontent({ type: 'INPUTSUBCONTENT', payload: e.target.value })} rows={5} defaultValue={props.input.subcontent} />
                        </div>
                        <div className="col">
                            <label htmlFor="content"><h3>Текст статьи</h3></label>
                            <ContentDText setcontenttrxt={setcontenttrxt} inputcontent={props.input.content} />
                        </div>
                    </div> : <div></div>
            }


        </div>)


}


const setPath = state => ({
    inputnames: state.inputnames,
    inputalias: state.inputalias,
    inputtitle: state.inputtitle,
    inputkeyword: state.inputkeyword,
    inputdescript: state.inputdescript,
    inputsubcontent: state.inputsubcontent,
    inputcontent: state.inputcontent
})

const toProps = dispatch => ({
    getinputnames: (e) => dispatch(e),
    getinputalias: (e) => dispatch(e),
    getinputtitle: (e) => dispatch(e),
    getinputkeyword: (e) => dispatch(e),
    getinputdescript: (e) => dispatch(e),
    getinputsubcontent: (e) => dispatch(e),
    getinputcontent: (e) => dispatch(e)
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Ubdateinput);