import React, { useState, useEffect } from 'react';
import { usersadmin_a, usersadminmaillogin_a } from '../redux/actions';
import { connect } from 'react-redux';
import Saves from '../components/Saves';
import Inputs from '../components/Inputs';
function Sittens(props) {
    const [email, setemail] = useState('');
    const [login, setlogin] = useState('');
    const [password, setpassword] = useState('');
    useEffect(() => {
        props.getuseradminmaillogin();
    }, [])
    useEffect(() => {
        setemail(props.useradminmaillogin[0]);
        setlogin(props.useradminmaillogin[1]);
    }, [props.useradminmaillogin]);
    function handleSubmit(event) {
        props.getuseradmin(email, login, password);
        event.preventDefault();
    }
    return (
        <div className="col">
            <form onSubmit={handleSubmit}>
                <Saves lincks={`/admin/index.html`} />
                <div className="row col">
                    <Inputs type="email" value={props.useradminmaillogin[0]} inputClass="" names="Почта" onChangeInput={setemail} />
                    <Inputs type="login" value={props.useradminmaillogin[1]} inputClass="" names="Логин" onChangeInput={setlogin} />
                    <Inputs type="password" value={password} inputClass="" names="Пароль" onChangeInput={setpassword} />
                </div>
            </form>
        </div>
    )
}
const setPath = state => ({
    useradmin: state.useradmin,
    useradminmaillogin: state.useradminmaillogin
})

const toProps = dispatch => ({
    getuseradmin: (email, login, password) => dispatch(usersadmin_a({ e: email, l: login, p: password })),
    getuseradminmaillogin: () => dispatch(usersadminmaillogin_a())
})
const mapDispatchToProps = toProps
const mapStateToProps = setPath

export default connect(mapStateToProps, mapDispatchToProps)(Sittens);